export function ComparisonDetailedRules(): JSX.Element {
  return (
    <Instruction contained>
      <Translate
        pt={
          <>
            J eliminamos todas as palavras idnticas, agora, elimine palavras invlidas ou similares.
            <br />
            Lembre-se que so consideradas dicas iguais palavras derivadas e conjugaes:{' '}
            <code>piloto = pilotar = pilotando</code>. Variaes como pluralidade, gnero e erros ortogrficos
            tambm devem ser eliminadas: <code>prncipe = princesa = prncipes = pryncipi</code>.
          </>
        }
        en={
          <>
            The app has already eliminated all identical clues, not it's time to eliminate all invalid clues.
            <br />
            Remember that the following clue types are considered invalid:
            <br />
            Variants: <code>pilot = pilots = piloting</code>.
            <br />
            Words on the same family: <code>prince = princess</code>
            <br />
            Misspelled or made-up words.
          </>
        }
      />
    </Instruction>
  );
}
